import React, { Component } from "react";
import { connect } from "react-redux";
import { setLanguage } from "../actions";

import legendIconNamesData from "../data/legendIconNamesData";
import galleryImagesData from "../data/galleryImagesData";
import activeIconsData from "../data/activeIconsData";
import tourLinksData from "../data/tourLinksData";
import videoLinksData from "../data/videoLinksData";
import mapTextData from "../data/mapTextData";
import videoTourTextData from "../data/videoTourTextData";

const legendIconNames = legendIconNamesData;
const galleryImages = galleryImagesData;
const activeIcons = activeIconsData;
const tourLinks = tourLinksData;
const videoLinks = videoLinksData;
const mapText = mapTextData;
const videoTourText = videoTourTextData;

export class InteractiveMap extends Component {
  constructor(props) {
    super(props);
    this.state = {
      iconHovered: false,
      iconHoveredIndex: 0,
      iconClickedIndex: 0,
      virtualTourWarningOpen: false,
      virtualTourOpen: false,
      categoriesWindowOpen: false,
      categoriesContentIndex: 0,
      galleryOpen: false,
      galleryImageClicked: false,
      clickedImageUrl: "./images/gallery/1_Aukuro/Kasinėjimai/1.jpg",
      carouselPosition: [0, 0, 0],
      imagesCategorySelected: 0,
      videoTourWindowOpen: false,
      contentType: "",
      videoTourClicked: 1,
      tourLinkAdress: "",
      categoryImageUrl: "",
      //categoryImageUrl: "./images/gallery/9_Kernaves amatininku kiemu rekonstrukcijos/Vaizdai/",
      categoryImagesArray: [0, 1, 2, 3, 4, 5, 6, 7],
      categoryImageClicked: 0,
      bigImageCarouselPosition: 0,
      windowWidth: 0,
      galleryImagesDisplayCount: 0,
      browserName: ""
    };
    this.activeIconsRefs = {};
    this.audioAboutRef = React.createRef();
    this.iframeRef = React.createRef();
    this.windowOpenImageRef = React.createRef();
  }

  mouseEnterActiveIcon = (index) => {
    this.setState({ iconHovered: true, iconHoveredIndex: index });
  };

  mouseLeaveActiveIcon = () => {
    this.setState({ iconHovered: false });
  };

  mouseClickContent = (contentType, index) => {
    this.setState({ contentType: contentType });
    if (index !== undefined) {
      this.setState({ categoriesContentIndex: index });
    }
    if (contentType === "tour") {
      this.setState({ tourLinkAdress: tourLinks[this.state.categoriesContentIndex] });
    } else if (contentType === "video") {
      this.setState({ tourLinkAdress: videoLinks[this.state.videoTourClicked][this.props.languageIndex] });
    }
    if (index === undefined) {
      this.setState({ imagesCategorySelected: 0, carouselPosition: [0, 0, 0] });
    }
  };

  buttonGalleryOpenClicked = () => {
    const initialGalleryState = this.state.galleryOpen;
    this.setState({ galleryOpen: !initialGalleryState });
    if (initialGalleryState) {
      this.setState({ imagesCategorySelected: 0 });
    }
  };

  buttonTourOpenClicked = () => {
    const initialState = this.state.virtualTourWarningOpen;
    this.setState({ virtualTourWarningOpen: !initialState });
  };

  galleryImageClicked = (imageUrl, categoryImageUrl, galleryImagesCount, imageIndex) => {
    if (galleryImagesCount !== undefined) {
      const categoryImagesArray = Array.from(Array(galleryImagesCount), (_, x) => x);
      this.setState({
        categoryImageUrl: categoryImageUrl,
        categoryImagesArray: categoryImagesArray,
        categoryImageClicked: imageIndex,
        bigImageCarouselPosition: imageIndex
      });
    }
    const initialImageState = this.state.galleryImageClicked;
    //       //document.webkitExitFullscreen();

    // if (this.state.browserName !== "safari/webkit") {
    //   if (this.state.windowWidth < 750) {
    //     if (initialImageState) {
    //       document.exitFullscreen();
    //     } else {
    //       this.windowOpenImageRef.current.requestFullscreen();
    //     }
    //   }
    // }

    this.setState({ galleryImageClicked: !initialImageState, clickedImageUrl: imageUrl });
  };

  mouseClickEnterTour = () => {
    //     // document.webkitExitFullscreen();

    const initialState = this.state.virtualTourOpen;
    // if (this.state.browserName !== "safari/webkit") {
    //   if (initialState) {
    //     document.exitFullscreen();
    //   } else {
    //     this.iframeRef.current.requestFullscreen();
    //   }
    // }
    this.setState({ virtualTourOpen: !initialState });
  };

  buttonCarouselClicked = (direction, galleryIndex, galleryImagesCount) => {
    let currentCarouselPosition = this.state.carouselPosition;
    let imageSlideLimit = galleryImagesCount * -1 + this.state.galleryImagesDisplayCount;
    if (direction === -1 && currentCarouselPosition[galleryIndex] > imageSlideLimit) {
      currentCarouselPosition[galleryIndex]--;
      this.setState({ carouselPosition: currentCarouselPosition });
    }
    if (direction === 1 && currentCarouselPosition[galleryIndex] < 0) {
      currentCarouselPosition[galleryIndex]++;
      this.setState({ carouselPosition: currentCarouselPosition });
    }
  };

  buttonVideoCarouselClicked = (direction) => {
    let currentVideoCarouselState = this.state.videoTourClicked;
    if (direction === -1 && currentVideoCarouselState !== 2) {
      this.setState({
        videoTourClicked: currentVideoCarouselState + 1,
        tourLinkAdress: videoLinks[currentVideoCarouselState + 1][this.props.languageIndex]
      });
    } else if (direction === 1 && currentVideoCarouselState !== 0) {
      this.setState({
        videoTourClicked: currentVideoCarouselState - 1,
        tourLinkAdress: videoLinks[currentVideoCarouselState - 1][this.props.languageIndex]
      });
    }
  };

  imageCarouselBigClicked = (direction) => {
    let currentImageCarouselPosition = this.state.bigImageCarouselPosition;
    let imagesCount = this.state.categoryImagesArray.length - 1;
    if (direction === -1 && currentImageCarouselPosition !== imagesCount) {
      this.setState({ bigImageCarouselPosition: currentImageCarouselPosition + 1 });
    } else if (direction === 1 && currentImageCarouselPosition !== 0) {
      this.setState({ bigImageCarouselPosition: currentImageCarouselPosition - 1 });
    }
  };

  languageBtnClicked = (language) => {
    this.props.setLanguage(language);
  };

  buttonImagesGalleryClicked = (index) => {
    this.setState({ imagesCategorySelected: index, carouselPosition: [0, 0, 0] });
  };

  buttonVideoTourWindowClicked = () => {
    const videoTourState = this.state.videoTourWindowOpen;
    this.setState({ videoTourWindowOpen: !videoTourState });
  };

  handleResize = (e) => {
    const windowWidth = window.innerWidth;
    let galleryImagesDisplayCount;
    if (windowWidth < 420) {
      galleryImagesDisplayCount = 3;
    } else {
      galleryImagesDisplayCount = 5;
    }
    this.setState((prevState) => {
      return {
        windowWidth,
        galleryImagesDisplayCount
      };
    });
  };

  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();

    let browserName = "";
    if (navigator.vendor.match(/google/i)) {
      browserName = "chrome/blink";
    } else if (navigator.vendor.match(/apple/i)) {
      browserName = "safari/webkit";
    } else if (navigator.userAgent.match(/firefox\//i)) {
      browserName = "firefox/gecko";
    } else if (navigator.userAgent.match(/edge\//i)) {
      browserName = "edge/edgehtml";
    } else if (navigator.userAgent.match(/trident\//i)) {
      browserName = "ie/trident";
    } else {
      browserName = navigator.userAgent + "\n" + navigator.vendor;
    }
    this.setState({ browserName: browserName });
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.state.iconHovered !== prevState.iconHovered || this.state.iconHoveredIndex !== prevState.iconHoveredIndex) {
      this.activeIconsRefs[this.state.iconHoveredIndex].classList.remove("active-icon-stop");
      this.activeIconsRefs[prevState.iconHoveredIndex].classList.remove("active-icon-stop");
      for (let i = 0; i < 8; i++) {
        this.activeIconsRefs[i].classList.remove("icons-stop");
      }
      if (this.state.iconHovered) {
        this.activeIconsRefs[this.state.iconHoveredIndex].classList.add("active-icon-stop");
        for (let i = 0; i < 8; i++) {
          this.activeIconsRefs[i].classList.add("icons-stop");
        }
      }
    }
  }

  render() {
    const buttonLanguageLtClass = this.props.language === "lithuanian" ? "button-language-clicked" : "";
    const buttonLanguageEnClass = this.props.language === "english" ? "button-language-clicked" : "";
    const displayedImages = galleryImages[this.state.categoriesContentIndex];
    const interactiveMapHeight = this.state.contentType !== "" && this.state.windowWidth < 420 ? "interactive-map-container-height" : "";

    return (
      <div className={`interactive-map-container ${interactiveMapHeight}`}>
        <div className="languages-container">
          <div className={`btn btn-language-lt  ${buttonLanguageLtClass}`} onClick={() => this.languageBtnClicked("lithuanian")}>
            LT
          </div>
          <div className={`btn btn-language-en  ${buttonLanguageEnClass}`} onClick={() => this.languageBtnClicked("english")}>
            EN
          </div>
        </div>
        <div className="map-images-container">
          <div className="river-image image"></div>
          <div className="map-image image"></div>
          <div className="active-icons-container">
            {activeIcons.map((activeIcon, index) => {
              return (
                <div
                  className="active-icon"
                  key={index}
                  ref={(ref) => {
                    this.activeIconsRefs[index] = ref;
                  }}
                  onMouseEnter={() => this.mouseEnterActiveIcon(index)}
                  onMouseLeave={() => this.mouseLeaveActiveIcon()}
                  onClick={() => this.mouseClickContent("about", index)}
                >
                  <div className="icon-scale">
                    <div className="icon-rotation">
                      <div
                        className="icon"
                        style={{
                          backgroundImage: `url("./images/icon-spinning.png")`
                        }}
                      ></div>
                    </div>
                  </div>
                  <div className="icon-title">{activeIcon[this.props.languageIndex]}</div>
                </div>
              );
            })}
          </div>
        </div>
        <div className="legend-container">
          <div className="legend-title">{mapText.legendTitle[this.props.languageIndex]}</div>
          <div className="icons-container">
            {legendIconNames.map((legendIconName, index) => {
              return (
                <div className="legend-item" key={index}>
                  <div className="icon-container">
                    <div
                      className="icon"
                      style={{
                        backgroundImage: `url("./images/Asset ${index + 2}.png")`
                      }}
                    ></div>
                  </div>
                  <div className="name">{legendIconName[this.props.languageIndex]}</div>
                </div>
              );
            })}
          </div>
        </div>
        {/* 360 Tour iframe window --------------------------------- */}
        <div
          className="tour-window-container"
          ref={this.iframeRef}
          //allowFullScreen
          style={{ display: this.state.virtualTourOpen ? "flex" : "none" }}
          key={this.state.videoTourClicked}
        >
          <div className="tour-window-content">
            <div className="btn btn-close" onClick={() => this.mouseClickEnterTour()}></div>
            <div className="virtual-reality-frame-container">
              <iframe
                key={this.state.videoTourClicked}
                //src={this.state.virtualTourOpen ? `${this.state.tourLinkAdress}` : ""}
                //src={"https://testinis.kernave.org/#media-name=Alytus_10"}
                src={"https://gluk.synology.me/tours/kernave%201-4/"}
                title="virtual tour"
                frameBorder="0"
                // allowFullScreen
              ></iframe>
            </div>
          </div>
        </div>
        {/* Categories window --------------------------------- */}
        <div className="modal-window-categories" style={{ display: this.state.contentType !== "" ? "flex" : "none" }}>
          <div className="buttons-container">
            <div className="buttons-categories">
              <div
                className={`btn btn-about ${this.state.contentType === "about" ? "btn-clicked" : ""}`}
                onClick={() => {
                  this.mouseClickContent("about");
                }}
              >
                <div className="icon">
                  <div className="text">{mapText.buttonAbout[this.props.languageIndex]}</div>
                </div>
              </div>
              <div className="line"></div>
              <div
                className={`btn btn-gallery ${this.state.contentType === "gallery" ? "btn-clicked" : ""}`}
                onClick={() => {
                  this.mouseClickContent("gallery");
                }}
              >
                <div className="icon"></div>
              </div>
              <div className="line"></div>

              <div
                className={`btn btn-tour ${this.state.contentType === "tour" ? "btn-clicked" : ""}`}
                onClick={() => {
                  this.mouseClickContent("tour");
                }}
              >
                <div className="icon"></div>
              </div>
              {this.state.categoriesContentIndex === 1 ? <div className="line"></div> : ""}
              {this.state.categoriesContentIndex === 1 ? (
                <div
                  className={`btn btn-video ${this.state.contentType === "video" ? "btn-clicked" : ""}`}
                  onClick={() => {
                    this.buttonVideoTourWindowClicked();
                    this.mouseClickContent("video");
                  }}
                >
                  <div className="icon"></div>
                </div>
              ) : (
                ""
              )}
            </div>
            {displayedImages.length > 1 ? (
              <div className="buttons-gallery-tabs" style={{ display: this.state.contentType === "gallery" ? "flex" : "none" }}>
                {displayedImages.map((imagesCategory, index) => {
                  let zIndexArray;
                  switch (this.state.imagesCategorySelected) {
                    case 0:
                      zIndexArray = [4, 3, 2, 1];
                      break;
                    case 1:
                      zIndexArray = [3, 4, 3, 2];
                      break;
                    case 2:
                      zIndexArray = [2, 3, 4, 3];
                      break;
                    case 3:
                      zIndexArray = [1, 2, 3, 4];
                      break;
                    default:
                      break;
                  }
                  let grayScaleValue = (zIndexArray[index] - 4) * -1 * 25;

                  return (
                    <div
                      className="btn btn-tabs"
                      key={index}
                      style={{
                        filter: `grayscale(${grayScaleValue}%)`,
                        left: this.state.windowWidth > 420 ? `calc(100% + ${-24 * index}px)` : `calc(100% + ${-20 * index}%)`,
                        //left: `calc(100% + ${-40 * index}px)`,
                        zIndex: zIndexArray[index],
                        width: this.state.categoriesContentIndex === 5 ? "200px" : "130px"
                      }}
                      onClick={() => {
                        this.buttonImagesGalleryClicked(index);
                      }}
                    >
                      <div className="tab-icons" style={{ display: this.state.imagesCategorySelected === index ? "none" : "flex" }}>
                        <div className="icon"></div>
                        <div className="icon"></div>
                      </div>
                      <div className="tab-title" style={{ display: this.state.imagesCategorySelected === index ? "flex" : "none" }}>
                        {imagesCategory[3][this.props.languageIndex]}
                      </div>
                    </div>
                  );
                })}
              </div>
            ) : (
              ""
            )}
            <div className="btn btn-close" onClick={() => this.mouseClickContent("")}>
              <div className="icon"></div>
            </div>
          </div>
          <div className="window-content window-content-about" style={{ display: this.state.contentType === "about" ? "flex" : "none" }}>
            <div className="overflow-wrapper">
              <div className="text-title title">{activeIcons[this.state.categoriesContentIndex][this.props.languageIndex]}</div>
              <div className="description">{activeIcons[this.state.categoriesContentIndex][this.props.languageIndex + 3]}</div>
            </div>
          </div>
          {/* gallery window ------------------------------ */}
          <div
            className="window-content window-content-gallery"
            style={{ display: this.state.contentType === "gallery" ? "flex" : "none" }}
          >
            <div className="galleries-container-wrapper">
              {displayedImages.map((imagesCategory, imagesCategoryIndex) => {
                return (
                  <div
                    className="galleries-container"
                    key={imagesCategoryIndex}
                    style={{ display: this.state.imagesCategorySelected === imagesCategoryIndex ? "flex" : "none" }}
                  >
                    {imagesCategory[1].map((galleryName, index) => {
                      const galleryImagesCount = imagesCategory[2][index];
                      const imagesArray = Array.from(Array(galleryImagesCount), (_, x) => x);
                      const folderName = imagesCategory[0];
                      const imageSlideLimit = galleryImagesCount * -1 + this.state.galleryImagesDisplayCount;
                      let galleryNameTranslate = galleryName;

                      if (this.props.languageIndex === 1) {
                        switch (galleryName) {
                          case "Kasinėjimai":
                            galleryNameTranslate = "Excavations";
                            break;
                          case "Radiniai":
                            galleryNameTranslate = "Findings";
                            break;
                          case "Vaizdai":
                            galleryNameTranslate = "Views";
                            break;
                          default:
                            break;
                        }
                      }
                      return (
                        <div className="gallery-content-wrapper" key={index}>
                          {this.state.imagesCategorySelected === imagesCategoryIndex ? (
                            <div className="gallery-content" key={`content${index}`}>
                              <div className="gallery-title">{galleryNameTranslate}</div>
                              <div className="gallery-images">
                                <div className="overflow-wrapper">
                                  <div
                                    className="gallery-images-carousel"
                                    style={{
                                      left:
                                        galleryImagesCount > this.state.galleryImagesDisplayCount
                                          ? `calc(${(this.state.carouselPosition[index] * 100) / this.state.galleryImagesDisplayCount}% + ${
                                              this.state.carouselPosition[index] * 1.5
                                            }px)`
                                          : "0%"
                                    }}
                                  >
                                    {imagesArray.map((image, index) => {
                                      let imageSmallUrl = `"./images/gallery small/${folderName}/${galleryName}/${index + 1}.jpg"`;
                                      let imageBigUrl = `"./images/gallery/${folderName}/${galleryName}/${index + 1}.jpg"`;
                                      let categoryImagesUrl = `./images/gallery/${folderName}/${galleryName}/`;
                                      return (
                                        <div
                                          className="image"
                                          key={`image${index}`}
                                          style={{
                                            backgroundImage: `url(${imageSmallUrl})`
                                          }}
                                          onClick={() => {
                                            this.galleryImageClicked(imageBigUrl, categoryImagesUrl, galleryImagesCount, index);
                                          }}
                                        >
                                          <div className="corner-icon" style={{ backgroundImage: `url("./images/Full screen.png")` }}></div>
                                        </div>
                                      );
                                    })}
                                  </div>
                                </div>
                                {galleryImagesCount > this.state.galleryImagesDisplayCount ? (
                                  <div className="carousel-buttons">
                                    <div
                                      className="btn btn-left"
                                      style={{ opacity: this.state.carouselPosition[index] === 0 ? 0.3 : 1 }}
                                      onClick={() => {
                                        this.buttonCarouselClicked(1, index, galleryImagesCount);
                                      }}
                                    ></div>
                                    <div
                                      className="btn btn-right"
                                      style={{ opacity: this.state.carouselPosition[index] === imageSlideLimit ? 0.3 : 1 }}
                                      onClick={() => {
                                        this.buttonCarouselClicked(-1, index, galleryImagesCount);
                                      }}
                                    ></div>
                                  </div>
                                ) : (
                                  ""
                                )}
                              </div>
                            </div>
                          ) : (
                            ""
                          )}
                        </div>
                      );
                    })}
                  </div>
                );
              })}
            </div>
          </div>
          {/* Tour warning window --------------------------------- */}
          <div className="window-content window-content-tour" style={{ display: this.state.contentType === "tour" ? "flex" : "none" }}>
            <div className="container">
              <div className="warning-text">
                <div className="line">{mapText.virtualTourWarning1[this.props.languageIndex]}</div>
                <div className="line">{mapText.virtualTourWarning2[this.props.languageIndex]}</div>
              </div>
              <div
                className="btn btn-confirm"
                onClick={() => {
                  this.mouseClickEnterTour();
                  this.mouseClickContent("");
                }}
              >
                {mapText.buttonConfirm[this.props.languageIndex]}
              </div>
            </div>
          </div>
          {/* Video window --------------------------------- */}
          <div className="window-content window-content-video" style={{ display: this.state.contentType === "video" ? "flex" : "none" }}>
            <div className="videos-container">
              <div className="carousel-buttons">
                <div
                  className="btn btn-left"
                  style={{ opacity: this.state.videoTourClicked === 0 ? 0.3 : 1 }}
                  onClick={() => {
                    this.buttonVideoCarouselClicked(1);
                  }}
                ></div>
                <div
                  className="btn btn-right"
                  style={{ opacity: this.state.videoTourClicked === 2 ? 0.3 : 1 }}
                  onClick={() => {
                    this.buttonVideoCarouselClicked(-1);
                  }}
                ></div>
              </div>
              <div className="overflow-wrapper">
                <div
                  className="video-carousel"
                  style={{ transform: `translate(${-50 + ((1 - this.state.videoTourClicked) * 100) / 3}%,-50%)` }}
                >
                  <div className="btn btn-video" onClick={() => this.mouseClickEnterTour()}>
                    <div className="icon"></div>
                  </div>
                  <div className="btn btn-video" onClick={() => this.mouseClickEnterTour()}>
                    <div className="icon"></div>
                  </div>
                  <div className="btn btn-video" onClick={() => this.mouseClickEnterTour()}>
                    <div className="icon"></div>
                  </div>
                </div>
              </div>
            </div>
            <div className="text-container">
              <div className="title">{videoTourText[this.state.videoTourClicked][0][this.props.languageIndex]}</div>
              <div className="description">{videoTourText[this.state.videoTourClicked][1][this.props.languageIndex]}</div>
            </div>
          </div>
        </div>
        <div
          className="modal-window-overlay"
          style={{
            display: this.state.contentType !== "" ? "flex" : "none"
          }}
        ></div>
        <div
          className="modal-window-open-image"
          ref={this.windowOpenImageRef}
          style={{ display: this.state.galleryImageClicked ? "flex" : "none" }}
        >
          <div className="image-container">
            <div className="overflow-wrapper">
              <div className="images-carousel" style={{ left: `${this.state.bigImageCarouselPosition * -100}%` }}>
                {this.state.categoryImagesArray.map((image, index) => {
                  index++;
                  let fullImageUrl = this.state.categoryImageUrl + index + ".jpg";
                  return <div className="image" key={index} style={{ backgroundImage: `url("${fullImageUrl}")` }}></div>;
                })}
              </div>
            </div>
            <div
              className="btn btn-close"
              onClick={() => {
                this.galleryImageClicked();
              }}
            ></div>
            <div className="carousel-buttons">
              <div
                className="btn btn-left"
                style={{ opacity: this.state.bigImageCarouselPosition === 0 ? 0.3 : 1 }}
                onClick={() => {
                  this.imageCarouselBigClicked(1);
                }}
              ></div>
              <div
                className="btn btn-right"
                style={{ opacity: this.state.bigImageCarouselPosition === this.state.categoryImagesArray.length - 1 ? 0.3 : 1 }}
                onClick={() => {
                  this.imageCarouselBigClicked(-1);
                }}
              ></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    language: state.setLanguage.language,
    languageIndex: state.setLanguage.languageIndex
  };
};

const mapDispatchToProps = {
  setLanguage
};

export default connect(mapStateToProps, mapDispatchToProps)(InteractiveMap);
